body {
  margin: 0;
}

html, body {
  height: 100%;
  margin: 0;
}

#bg {
  width: 100%;
  height: 100%;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

#topBanner {
  color: #000;
  text-align: center;
  z-index: 2000;
  backdrop-filter: blur(4px);
  background: #ffffff80;
  border-radius: 8px;
  padding: 10px 18px;
  font-family: "Press Start 2P", monospace;
  font-size: 14px;
  font-weight: 400;
  line-height: 2;
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 4px 12px #0000001f;
}

@media (width <= 520px) {
  #topBanner {
    padding: 8px 12px;
    font-size: 14px;
    top: 12px;
  }
}

#bottomBanner {
  color: #000;
  text-align: center;
  z-index: 2000;
  backdrop-filter: blur(4px);
  background: #ffffff80;
  border-radius: 6px;
  padding: 6px 12px;
  font-family: "Press Start 2P", monospace;
  font-size: 10px;
  font-weight: 400;
  position: fixed;
  bottom: 12px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 8px #0000001f;
}

@media (width <= 520px) {
  #bottomBanner {
    padding: 5px 8px;
    font-size: 9px;
    bottom: 8px;
  }
}
